import React from 'react';
import './App.css';

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      innerValue: "0",
      outerValue: "0",
      lastCharIsOperator: false,
      minusNumberOperator: false,
      equalsClicked: false,
      decimalPointAdded: false,
      numbers: [],
      operands: [],
      keystrokes: {
        1: "one",
        2: "two",
        3: "three",
        4: "four",
        5: "five",
        6: "six",
        7: "seven",
        8: "eight",
        9: "nine",
        0: "zero",
        "-": "subtract",
        "+": "add",
        "/": "divide",
        "*": "multiply",
        ".": "decimal",
        ",": "decimal",
        "Enter": "equals",
        "Delete": "clear",
        "Backspace": "clear"
      },   
    }

  document.addEventListener("keydown", (event) => {
    if (this.state.keystrokes.hasOwnProperty(event.key)) {
      document.getElementById(this.state.keystrokes[event.key]).click()
      }
    })
  }
  
  handleChange = (event) => {
    event.persist()
// RESET CALCULATOR
    if (event.target.innerHTML === "AC") {
      this.setState ({
        innerValue: "0",
        outerValue: "0",
        lastCharIsOperator: false,
        minusNumberOperator: false,
        equalsClicked: false,
        decimalPointAdded: false,
        numbers: [],
        operands: []
      })
// ENTER NUMBER
    } else if (/\d/.test(event.target.innerHTML)) {
      if (this.state.equalsClicked) {
        this.setState ({
          innerValue: event.target.innerHTML,
          outerValue: event.target.innerHTML,
          equalsClicked: false,
        })
      } else if (this.state.outerValue === "0") {
          if (parseInt(event.target.innerHTML) === 0) {
            return null
          } else {
            this.setState(prevState => {
              return ({
                innerValue: prevState.innerValue.slice(0, prevState.innerValue.length - 1) + event.target.innerHTML,
                outerValue: event.target.innerHTML
              })
            })
          }
      } else if (this.state.lastCharIsOperator) {
          this.setState(prevState => {
            return ({
              innerValue: prevState.innerValue + event.target.innerHTML,
              outerValue: event.target.innerHTML,
              lastCharIsOperator: false,
              minusNumberOperator: false
            })
          })
      } else {
          this.setState(prevState => {
            return ({
              innerValue: prevState.innerValue + event.target.innerHTML,
              outerValue: prevState.outerValue + event.target.innerHTML
            })
          })
        }
    } 
// ENTER OPERATOR
      else if (/[/*\-+]/.test(event.target.innerHTML)) {
        if (event.target.innerHTML === "-" && this.state.innerValue === "0") {
          this.setState ({
            innerValue: event.target.innerHTML,
            outerValue: event.target.innerHTML
          })
        } else if (this.state.equalsClicked) {
          this.setState (prevState => {
            return ({
              innerValue: prevState.outerValue + event.target.innerHTML,
              outerValue: event.target.innerHTML,
              lastCharIsOperator: true,
              equalsClicked: false,
              decimalPointAdded: false,
            })
          })
        } else if (this.state.lastCharIsOperator) {
            if (event.target.innerHTML === "-" && (this.state.outerValue === "*" || this.state.outerValue === "/" || this.state.outerValue === "+")) {
              this.setState(prevState => {
                return ({
                  innerValue: prevState.innerValue + event.target.innerHTML,
                  outerValue: event.target.innerHTML,
                  minusNumberOperator: true
                })
              })
            } else if (event.target.innerHTML !== "-" && this.state.minusNumberOperator) {
              this.setState(prevState => {
                return ({
                  innerValue: prevState.innerValue.slice(0, prevState.innerValue.length - 2) + event.target.innerHTML,
                  outerValue: event.target.innerHTML,
                  minusNumberOperator: false
                })
              })
            } else {
              this.setState(prevState => {
                return ({
                  innerValue: prevState.innerValue.slice(0, prevState.innerValue.length - 1) + event.target.innerHTML,
                  outerValue: event.target.innerHTML
                })
              })
            }
        } else {
            this.setState(prevState => {
              return ({
                innerValue: prevState.innerValue + event.target.innerHTML,
                outerValue: event.target.innerHTML,
                lastCharIsOperator: true,
                decimalPointAdded: false
              })
            })
        }
    } 
// ENTER DECIMAL POINT
    else if (/\./.test(event.target.innerHTML)) {
      if (this.state.equalsClicked) {
          this.setState({
            innerValue: "0.",
            outerValue: "0.",
            decimalPointAdded: true,
            equalsClicked: false
          })
      } else if (this.state.decimalPointAdded) {
          return null
      } else if (this.state.lastCharIsOperator) {
        this.setState(prevState => {
          return ({
            innerValue: prevState.innerValue + "0.",
            outerValue: "0.",
            decimalPointAdded: true,
            lastCharIsOperator: false
          })
        })
      } else {
        this.setState(prevState => {
          return ({
            innerValue: prevState.innerValue + event.target.innerHTML,
            outerValue: prevState.outerValue + event.target.innerHTML,
            decimalPointAdded: true,
            lastCharIsOperator: false
          })
        })
      }
    } 
// ENTER EQUAL
    else if (/=/.test(event.target.innerHTML)) {
        if (this.state.equalsClicked) {
          return null
        } else {
          let numbersLogged = this.state.innerValue.match(/^-\d+\.?\d*|(?<=[/*+])-\d+\.?\d*|\d+\.?\d*/g);
          let operandsLogged = this.state.innerValue.match(/(?<=\d)[/*\-+]/g);
          var multiplyDivideIndex = [];
          (function() {
            for (let i = 0; i < numbersLogged.length - 1; i++) {
              let operand = operandsLogged[i]
              let firstNum = parseFloat(numbersLogged[i])
              let secondNum = parseFloat(numbersLogged[i + 1])
              if (operand === "*") {
                numbersLogged[i + 1] = firstNum * secondNum
                multiplyDivideIndex.unshift(i)
              } else if (operand === "/") {
                numbersLogged[i + 1] = firstNum / secondNum
                multiplyDivideIndex.unshift(i)
              }
            }
            for (let i = 0; i < multiplyDivideIndex.length; i++) {
              numbersLogged.splice(multiplyDivideIndex[i], 1)
              operandsLogged.splice(multiplyDivideIndex[i], 1)
            }
          }())
          var sum = parseFloat(numbersLogged[0]);
          (function() {
            for (let i = 0; i < numbersLogged.length - 1; i++) {
              let operand = operandsLogged[i]
              let secondNum = parseFloat(numbersLogged[i + 1])
              if (operand === "+") {
                sum += secondNum
              } else if (operand === "-") {
                sum -= secondNum
              } 
            }
          }())
          sum = parseFloat((sum).toFixed(12));
        }
       this.setState(prevState => {
         return ({
           innerValue: prevState.innerValue + "=" + sum,
           outerValue: sum,
           equalsClicked: true,
           lastCharIsOperator: false,
           minusNumberOperator: false,
           decimalPointAdded: false
         })
      })
    }
  }
  
  render() {
    return (
      <div className="App">
      <div className="mx-auto vh-100 vw-100 text-white p-0 border border-secondary d-flex flex-column" style={{maxWidth: 400, maxHeight: 600, backgroundColor: "black"}}>
        <div className="d-flex justify-content-end" id="displayInner" style={{height: 40}}>
          <p className="pr-1">{this.state.innerValue}</p>
        </div>
        <div className="d-flex justify-content-end align-items-center" id="display"style={{height: 60}}>
          <p className="pr-1 m-0">{this.state.outerValue}</p>
          </div>
        <div className="flex-fill row p-0 m-0" style={{maxHeight: 498}}>
          <button className="col-9 btn-danger" id="clear" onClick={this.handleChange}>AC</button>
          <button className="col-3 btn-light" id="divide" onClick={this.handleChange}>/</button>
          <button className="col-3 btn-light" id="seven" onClick={this.handleChange}>7</button>
          <button className="col-3 btn-light" id="eight" onClick={this.handleChange}>8</button>
          <button className="col-3 btn-light" id="nine" onClick={this.handleChange}>9</button>
          <button className="col-3 btn-light" id="multiply" onClick={this.handleChange}>*</button>
          <button className="col-3 btn-light" id="four" onClick={this.handleChange}>4</button>
          <button className="col-3 btn-light" id="five" onClick={this.handleChange}>5</button>
          <button className="col-3 btn-light" id="six" onClick={this.handleChange}>6</button>
          <button className="col-3 btn-light" id="subtract" onClick={this.handleChange}>-</button>
          <button className="col-3 btn-light" id="one" onClick={this.handleChange}>1</button>
          <button className="col-3 btn-light" id="two" onClick={this.handleChange}>2</button>
          <button className="col-3 btn-light" id="three" onClick={this.handleChange}>3</button>
          <button className="col-3 btn-light" id="add" onClick={this.handleChange}>+</button>
          <button className="col-6 btn-light" id="zero" onClick={this.handleChange}>0</button>
          <button className="col-3 btn-light" id="decimal" onClick={this.handleChange}>.</button>
          <button className="col-3 btn-primary" id="equals" onClick={this.handleChange}>=</button>
        </div>
      </div>
    </div>
  );
}
}

export default App;


// User Story #1: My calculator should contain a clickable element containing an = (equal sign) with a corresponding id="equals".

// User Story #2: My calculator should contain 10 clickable elements containing one number each from 0-9, with the following corresponding IDs: id="zero", id="one", id="two", id="three", id="four", id="five", id="six", id="seven", id="eight", and id="nine".

// User Story #3: My calculator should contain 4 clickable elements each containing one of the 4 primary mathematical operators with the following corresponding IDs: id="add", id="subtract", id="multiply", id="divide".

// User Story #4: My calculator should contain a clickable element containing a . (decimal point) symbol with a corresponding id="decimal".

// User Story #5: My calculator should contain a clickable element with an id="clear".

// User Story #6: My calculator should contain an element to display values with a corresponding id="display".

// User Story #7: At any time, pressing the clear button clears the input and output values, and returns the calculator to its initialized state; 0 should be shown in the element with the id of display.

// User Story #8: As I input numbers, I should be able to see my input in the element with the id of display.

// User Story #9: In any order, I should be able to add, subtract, multiply and divide a chain of numbers of any length, and when I hit =, the correct result should be shown in the element with the id of display.

// User Story #10: When inputting numbers, my calculator should not allow a number to begin with multiple zeros.

// User Story #11: When the decimal element is clicked, a . should append to the currently displayed value; two . in one number should not be accepted.

// User Story #12: I should be able to perform any operation (+, -, *, /) on numbers containing decimal points.

// User Story #13: If 2 or more operators are entered consecutively, the operation performed should be the last operator entered (excluding the negative (-) sign). For example, if 5 + * 7 = is entered, the result should be 35 (i.e. 5 * 7); if 5 * - 5 = is entered, the result should be -25 (i.e. 5 x (-5)).

// User Story #14: Pressing an operator immediately following = should start a new calculation that operates on the result of the previous evaluation.

// User Story #15: My calculator should have several decimal places of precision when it comes to rounding (note that there is no exact standard, but you should be able to handle calculations like 2 / 7 with reasonable precision to at least 4 decimal places).

// Note On Calculator Logic: It should be noted that there are two main schools of thought on calculator input logic: immediate execution logic and formula logic. Our example utilizes formula logic and observes order of operation precedence, immediate execution does not. Either is acceptable, but please note that depending on which you choose, your calculator may yield different results than ours for certain equations (see below example). As long as your math can be verified by another production calculator, please do not consider this a bug.

// EXAMPLE: 3 + 5 x 6 - 2 / 4 =

// Immediate Execution Logic: 11.5
// Formula/Expression Logic: 32.5